.buttonGameDetails{
    position: absolute;
    margin-top: -24px;
    margin-left: -31.1%;
    background: #eecb00;
    font-size: 12px;
    padding: 5px;
    border: none;
    text-decoration: none;
    color:rgb(255, 255, 255);
    box-shadow: inset -3px -3px 0px 0px #E59400;
}

.buttonGameDetails:hover, .buttonGameDetails:focus{
    background: #d9af07;
    color: rgb(221, 221, 221);
    box-shadow: inset -3px -3px 0px 0px #E59400;
}

.buttonGameDetails:active {
    box-shadow: inset 3px 3px 0px 0px #E59400;
}

.buttonGameDetails:before, .buttonGameDetails:after {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    box-sizing: content-box;
}

.buttonGameDetails:before {
    top: -3px;
    left: 0;
    border-top: 3px rgb(70, 70, 70) solid;
    border-bottom: 3px rgb(71, 71, 71) solid;
}

.buttonGameDetails:after {
    left: -3px;
    top: 0;
    border-left: 3px rgb(71, 71, 71) solid;
    border-right: 3px rgb(70, 70, 70) solid;
}

